.header{
    display: flex;
    width: 100%;
    align-items: center;
    height: 60px;
    justify-content: space-between;
    position: sticky;
    top: 0;
    z-index: 100;
    background-color: rgb(31, 30, 30);
    border-bottom: 1px solid rgb(95, 95, 95);
opacity : 0.9;



}
@supports(-webkit - backdrop - filter : none)or(backdrop - filter : none)
{
    .header{
-webkit-backdrop-filter : blur(2 em);
backdrop-filter : blur (2 em)


    }
}


.header-left{
    margin-left: 20px;

}

.header-logo{
    width: 90px;
    margin-left: 15px;
    margin-bottom: 3px;
}

.header-right{
    /* position: absolute; */
    display: flex;
    width: 170px;
    align-items: center;
    justify-content: space-between;
    right: 3%;
    margin-right: 20px;

}

.header-avatar{
    margin-left: 10px;
}

.header-icon{
    color: rgb(34, 32, 32);
}

.header-icon:hover{
    color: rgb(221, 9, 9) !important;
    font-size: 30px !important;
    transition: all 0.3s;
    cursor:pointer

}


.header-middle{
    display: flex;
    align-items: center;
    width: 35%;
    border: 1px solid rgb(153, 153, 153);
    border-radius: 3px;
    height: 25px;
}

.header-middle > input {
    flex: 1;
    border: none;
    padding-left: 15px;
}

.header-middle > input:focus{
    outline:invert;
}

.header-middle > input::placeholder{
    letter-spacing: 0.1rem;
    font-weight: 200;
}



.header-input-icon{
    width: 11% !important;
    background-color: #fafafa;
    border: 1px solid rgb(54, 54, 54);
    border-right: none;
    color: gray;
    display: flex;
    align-items: center;
    height: 125%;
    border-radius: 2px ;
}

.search-icon{
    font-size: 20px !important;
    margin-left: 13px;
}